import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import './marop.css';


const osName = platform();

const Mar10 = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="marophons">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>День 10
			
		</PanelHeader>
		<div className="container">

		<div className="cont1">
		<h1 className="yprag">Подтягивания широким хватом: 6 х 7</h1>
		<div className="chill"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>


		<div className="cont2">
		<h1 className="yprag">Подтягивания: 4 х 10</h1>
		<div className="chill"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>

		<div className="cont3">
		<h1 className="yprag">Брусья: 5 х 8</h1>
		<div className="chill"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>

		<div className="cont4">
		<h1 className="yprag">Отжимания: 4 х 3</h1>
		<div className="chill"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>

		<div className="cont5">
		<h1 className="yprag">Австралийские подтягивания: 4 х 6</h1>
		<div className="chill"><h1 className="chl">Отдых:3 минуты</h1></div>
		</div>

		</div>	


		</Panel>
);

Mar10.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Mar10;
