import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import View from '@vkontakte/vkui/dist/components/View/View';

import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Progsapp from './panels/Progsapp';
import Marophons from './panels/Marophons';
import Program from './panels/Program';
import Prog1 from './panels/Prog1';
import Prog2 from './panels/Prog2';
import Prog3 from './panels/Prog3';
import Prog4 from './panels/Prog4';
import Mar1 from './panels/marophon/Mar1';
import Mar2 from './panels/marophon/Mar2';
import Mar3 from './panels/marophon/Mar3';
import Mar4 from './panels/marophon/Mar4';
import Mar5 from './panels/marophon/Mar5';
import Mar6 from './panels/marophon/Mar6';
import Mar7 from './panels/marophon/Mar7';
import Mar8 from './panels/marophon/Mar8';
import Mar9 from './panels/marophon/Mar9';
import Mar10 from './panels/marophon/Mar10';




const App = () => {



	const [activePanel, setActivePanel] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(null);

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await connect.send('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);
   

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
		};

	

	return (
		<View activePanel={activePanel} popout={popout}>

			<Home id='home' fetchedUser={fetchedUser} go={go} />
			
			<Progsapp id='progsapp' go={go} />
			<Marophons id='marophons' go={go} />
			<Program id='program' go={go} />
			<Prog1 id='prog1' go={go} />
			<Prog2 id='prog2' go={go} />
			<Prog3 id='prog3' go={go} />
			<Prog4 id='prog4' go={go} />
			<Mar1 id='mar1' go={go} />
			<Mar2 id='mar2' go={go} />
			<Mar3 id='mar3' go={go} />
			<Mar4 id='mar4' go={go} />
			<Mar5 id='mar5' go={go} />
			<Mar6 id='mar6' go={go} />
			<Mar7 id='mar7' go={go} />
			<Mar8 id='mar8' go={go} />
			<Mar9 id='mar9' go={go} />
			<Mar10 id='mar10' go={go} />

			
		</View>
	);
}

export default App;

